.section-partners{
    padding-top:90px;
    margin-top: 50px;
    padding-bottom:80px;
    background-color:lighten($gray,2%);

    position: relative;
    .partners-wrapper{
        padding-bottom:50px;
        overflow: hidden;
        .partner-item{
            max-height:55px;
            width:100px;
            img{
                height:100%;
                max-height:55px;
                max-width: 170px;
                object-fit: contain;
            }
        }

        .slick-dots{
        
            bottom:-45px;
        }
    }

   
    .section-right{
        .section-header{
            text-align: left;
        }
    }
    @include custom_max(991px){
        margin-top:0;
    }
    @include custom_max(767px){
        padding-bottom:80px;
    }
}