.section-banner{
    position: relative;
    background: rgb(0,140,158); /* Old browsers */
    background: -moz-linear-gradient(45deg, rgba(0,140,158,1) 0%, rgba(0,193,219,1) 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(45deg, rgba(0,140,158,1) 0%,rgba(0,193,219,1) 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(45deg, rgba(0,140,158,1) 0%,rgba(0,193,219,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#008c9e', endColorstr='#00c1db',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
    overflow: hidden;
    .background{
        width:100%;
        position: absolute;
        bottom:-10px;
        left:0;
        right:0;
    }

    .container{
       
    }
    .triangle{
        &-1,&-2,&-3{
            position: absolute;
        
        }
        &-1{
            top:50%;
            transform:translateY(-50%);
            max-width:80px;
            left:0;
        }
        &-2{
            bottom: 35%;
            right: 35%;
            max-width: 50px;
        }

        &-3{
            bottom:15px;
            right:25px;
            max-width:40px;
            z-index: 1;
        }
    }

    &__content{
        padding:15% 0 40%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
 
        z-index: 1;
        .text{
            color: #fff;
            max-width: 530px;
            .hero-text{
               margin:0 0 40px;
               font:35px $nunitob;
            }
            .description{
                color:#fff;
                line-height: 1.7;
                margin-bottom: 30px;
            }

            form{
                .input-group{
                    &-append{
                        padding-left:15px;
                    }
                }
                input{
                    height:50px;
                    border-radius: 50px !important;
                    border:none;
                    padding-left:25px;
                    padding-right:25px;
                    @include placeholder{
                        font-size:14px;
                        color:$gray2;
                    }
                }

                .btn{
                    background-color:$baseColor;
                    height:50px;
                    padding:0 40px;
                    color:#fff;
                    border:none;
                    border-radius: 50px !important;
                    font-size: 14px;
                    cursor: pointer;
                    transition:all 0.4s;
                    &:hover{
                        background-color: darken($baseColor,10%);
                    }
                }
            }
        }

        .image{
            position: absolute;
            right:0;
            top:35%;
            transform:translateY(-50%);
            max-width: 600px;
        }

        
    }
    @include custom_max(1199px){
        &__content{
            .image{
                max-width: 400px;
            }
            .text{
                max-width: 400px;
            }
        }
    }
    @include custom_max(991px){
        &__content{
            .image{
                max-width: 300px;
            }
        }
    }
    @include custom_max(767px){
        &__content{
            padding:40% 0  40%;
            text-align: center;
            position: relative;
            .text{
                margin:0 auto;
                max-width: 100%;
                .hero-text{
                    font-size:25px;
                }
            }
            .image{
                display: none;
            }
        }
    }
    @include custom_max(512px){
        &__content{
            .text{
                form{
                    .input-group{
                        flex-direction: column;
                        justify-content: center;
                        align-items: center;
                        &-append{
                            padding-left:0;
                        }
                        input{
                            width:100%;
                            margin-bottom:15px;
                        }
                    }
                    
                }
            }
        }
    }
}