.section-decision{
    padding-bottom:0;
    margin-bottom: 120px;
    position: relative;
    // @extend .bg-gray;
    overflow: hidden;
    &::before{
        content:"";
        display: none;
        position:absolute;
        top:-54px;
        left:0;
        right:0;
         bottom:0;
         height:40%;
         background: -moz-linear-gradient(top, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 15%, rgba(255,255,255,0) 51%, rgba(255,255,255,0) 53%); /* FF3.6-15 */
         background: -webkit-linear-gradient(top, rgba(255,255,255,1) 0%,rgba(255,255,255,1) 15%,rgba(255,255,255,0) 51%,rgba(255,255,255,0) 53%); /* Chrome10-25,Safari5.1-6 */
         background: linear-gradient(to bottom, rgba(255,255,255,1) 0%,rgba(255,255,255,1) 15%,rgba(255,255,255,0) 51%,rgba(255,255,255,0) 53%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
         filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#00ffffff',GradientType=0 );
  
    }
    .pattern{
        position: absolute;
        bottom:-10px;
        left:0;
        right:0;
    }
    
    .jumbotron{
        background-color:#fff;
        border:none;
        border-radius: 25px;
        box-shadow: 0 5px 25px 5px rgba(#000,0.05);
        position: relative;
        z-index:1;
        .logo{
            max-width: 195px;
            margin:0 auto 40px;
        }

    }

    .tree{
        position: relative;
        @include flexbox();
        @include flex-direction(row);
        @include justify-content(space-between);
        margin-left:-25px;
        margin-right:-25px;
        padding:45px 25px 0;
        &::before{
            content:"";
            border-top:2px solid $thirdColor;
            display:block;
            position: absolute;
            left:14.25%;
            right:14.25%;
            top:0;
            opacity:0.5;
        }
        &-item{
           position: relative;
           max-width:33.333%;
           @include flex-basis(33.333%);
           padding:0 25px;
           .parent-item{
                position: relative;
              
               .text{
                background: #fff;
                border-radius:100px;
                padding:20px 45px;
                box-shadow:0 2px 25px -7px rgba(146, 146, 146, 0.4);
                font:14px $nunitor;
                text-align: center;
                position: relative;
                line-height:1;

                &::before{
                    content:"";
                    height:25px;
                    width:25px;
                    display: block;
                    border-radius:100px;
                    border:3px solid $baseColor;
                    position: absolute;
                    top:50%;
                    transform:translateY(-50%);
                    left:15px;
                }
               }
               &::before{
                   content:"";
                   display:block;
                   position: absolute;
                   bottom:100%;
                   left:50%;
                   transform:translateX(-50%);
                   width:15px;
                   height:35px;
                   margin-bottom:10px;
                   background:url('/images/arrow-down.png') no-repeat center center/contain;
               }
              
               &::after{
                   content:"";
                   display: block;
                   position: absolute;
                   top:50%;
                   left:100%;
                   width:40px;
                   margin-left:5px;
                   height:15px;
                   transform: translateY(-50%);
                   background: url('/images/arrow-right.png') no-repeat center center/contain;
               }

               &:last-child{
                   &::after{
                       display: none;
                   }
               }

           }

           .children-item{
                position: relative;
                left:100%;
                transform:translateX(-40%);
                display: inline-block;
                margin-top: 85px;
                .text{
                    background: #fff;
                    border-radius:100px;
                    padding:15px 35px;
                    box-shadow:0 2px 25px -7px rgba(146, 146, 146, 0.4);
                    font:12px $nunitor;
                    text-align: center;
                    position: relative;
                    z-index: 1;
                    line-height:1;
                    &::before{
                        content:"";
                        height:15px;
                        width:15px;
                        display: block;
                        border-radius:100px;
                        border:2px solid $thirdColor;
                        position: absolute;
                        top:50%;
                        transform:translateY(-50%);
                        left:15px;
                    }
                }

                &::after{
                    content:"";
                    display: block;
                    position: absolute;
                    bottom:100%;
                    width:225px;
                    height: 90px;
                    background:url('/images/oval-arrow.png') no-repeat center center/ contain;
                    margin:0 auto -10px;
                    left:50%;
                    transform: translateX(-50%);
                    z-index: -9;
                    margin-left:5px;
                }
           }
        }
    }
    .triangle{
        &-4,&-5,&-6,&-10,&-9,&-8{
            position: absolute;
            z-index:-1;
        }
        &-4{
            top:20%;
            left:0;
            max-width: 100px;
        }
        &-5{
            max-width: 80px;
            right:0;
            bottom:25%;
        }
        &-6{
            max-width: 150px;
            right:2%;
            bottom:1%;
        }
        &-10{
            top:10%;
            right:10%;
        }
        &-9{
            top:9%;
            right:7%;
            max-width: 170px;
        }
        &-8{
            bottom:22%;
            left:8%;
            max-width:80px;

        }
    }

    @include custom_max(1199px){
        .jumbotron{
            padding-left:10px;
            padding-right:10px;
        }

        .tree{
            padding-left:10px;
            padding-right:10px;
            .parent-item{
                .text{
                    padding:15px 30px;
                    
                    &::before{
                        height:15px;
                        width:15px;
                      
                    }
                }
            }
        }
    }

    @include custom_max(991px){
        padding-bottom:80px;
        margin-bottom:0;
        .tree{
            padding-left:0;
            padding-right: 0;
            &::before{
                left:93px;
                right:77px;
            }
            .parent-item{
                &::before{
                    margin-bottom:20px;
                }
                .text{
                    padding:5px 10px;
                    font-size:12px;
                    min-height:50px;
                    display: flex;
                    margin-top: 10px;
                    align-items: center;
                    justify-content: center;
                    &::before{
                        position: absolute;
                        margin:0 auto;
                        left:0;
                        right:0;
                        top:-18px;
                        transform:translateY(0);
                    }
                }
            }

            .children-item{
                margin-top:45px;
                &::after{
                    width:150px;
                    margin-bottom: -20px;
                }
                .text{
                    padding-left:35px;
                    padding-right:35px;
                    white-space: nowrap;
                    &::before{
                        height:10px;
                        width:10px;
                    }
                }
            }
        }
    }

    @include custom_max(767px){
        
        .tree{
            flex-direction: column;
           margin-left:0;
           margin-right: 0;
           padding-top:30px;
           &-item{
               width:auto;
               max-width: 100%;
               flex: 0 0 100%;
           }
           &::before{
               left:0;
               right:0;
           }
            .parent-item{
                max-width: 150px;
                &::after{
                    display: none;
                }
                &::before{
                    margin-bottom: 5px;
                }
                .text{

                    min-height: auto;
                    padding:12px 25px 12px 40px;
                    white-space: nowrap;
                    min-width: 150px;
                    &::before{
                        top:50%;
                        transform: translateY(-50%);
                        left:5px;
                        right: auto;
                        
                        position: absolute;
                    }
                }
            }

            .children-item{
                margin-top:0;
                transform:translateX(0);
                margin-left: 0;
                left:180px;
                &::after{
                    transform: translate(0) rotate(270deg) scaleX(-1);
                    left: -56px;
                    top: -18px;
                    width: 83px;

                }
            }
        }
    }

    @include custom_max(512px){
        .tree{
            &-item{
                padding-left:10px;
                padding-right:10px;
            }
            .children-item{
                left:100px;
                &::after{
                    display: none;
                }
                .text{
                    top:5px;
                }
            }
        }
    }


    .features{
        margin-top:80px;
        .item{
            padding-left:45px;
            padding-right: 45px;
            align-items: center;
            justify-content: center;
            margin-bottom:80px;
            .image{
                padding-left:25px;
                padding-right:30px;
                max-width:480px;
            }
            .text{
                padding-left: 30px;
                padding-right: 25px;
                max-width: 450px;
            }

            h3{
                font:25px $nunitor;
                color:$secondaryColor;
                margin-bottom:20px;
            }
            p{
                font-size:18px;
                line-height: 1.7;
                color:$secondaryColor;
            }

            &:nth-child(even){
                flex-direction: row-reverse;
            }
            &:last-child{
                margin-bottom: 0;
            }
        }

        @include custom_max(991px){
            .item{
                padding-left:0;
                padding-right:0;
                margin-bottom:30px;
                .image,.text{
                    text-align: center;
                    padding-left:0;
                    padding-right:0;
                    margin:0 auto;
                }
                .image{
                    margin-bottom:30px;
                    max-width: 300px;
                }
                .text{
                    padding-left:25px;
                    padding-right:25px;

                }
                h3{
                    font-size:20px;
                }
                p{
                    font-size:16px;
                }
            }
        }
    }
}