.card{
    background-color:#fff;
    padding:25px 15px;
    box-shadow:0 5px 30px 10px rgba(0, 0, 0, 0.078);
    border-radius: 15px;
    border:none;
    margin-bottom: 30px;

    &-body{
        padding: 25px 10px 15px;
        
    }
    &-title{
        font:18px $nunitob;
        color:$secondaryColor;
        margin-bottom:5px;
    }
    &-text{
        font:14px $nunitor;
        color:#697d7f;
        line-height: 1.6;
    }
    &-img{
        max-width:120px;
        margin: 10px 0 10px 5px;
    }
}