.btn{
    background-color: $baseColor;
    padding:10px 35px;   
    font-size:14px;
    display: inline-block;
    transition:all 0.4s;
    &.btn-base{
        background-color:$baseColor;
        color:#fff;
        &:hover{
            background-color: darken($baseColor,10%);
        }
    }
    &.btn-round{
        border-radius: 100px;
    }
}