html,body{
    line-height: 1.6;
}
ul{list-style:none;margin:0}

img{
    max-width:100%;
}
input{
    &:focus{
        outline:none;
    }
}
.section{
    padding:150px 0;
    &-header{
        margin:0 0 50px;
        text-align: center;
    }
    &-title{
        font:30px $nunitosb;
        color:$secondaryColor;
        margin:0 0 20px;
        &-sub{
            color:$text;
        }
    }
}


.bg-gray{
    background-color:#F8FBFE;
}

.slick-dots{
    li{
        margin:0;
        width:15px;
        height:15px;
        &::before{
            display: none;
        }
        button{
            content:"";
            text-indent:-9999px;
            height:5px;
            width:5px;
            padding:0;
            background-color:$gray2;
            border-radius: 100px;
        }
        &.slick-active{
            button{
                background-color:$thirdColor;
                height:7px;
                width:7px;
            }
        }
    }
}


@include custom_max(991px){
    .section{
        padding:80px 0;
        &-header{
            margin:0 0 30px;
        }
        &-title{
            font:25px $nunitosb;
            margin:0 0 15px;

        }
    }
    
}