.main-footer{
    overflow: hidden;
    background: rgb(0,140,158); /* Old browsers */
    background: -moz-linear-gradient(-45deg, rgba(0,140,158,1) 0%, rgba(0,193,219,1) 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(-45deg, rgba(0,140,158,1) 0%,rgba(0,193,219,1) 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(135deg, rgba(0,140,158,1) 0%,rgba(0,193,219,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#008c9e', endColorstr='#00c1db',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
    color:#fff;
    
    .footer-pattern{

        margin-bottom:70px;
    }

    .footer-top-content{
        padding-left:25px;
        padding-right:25px;
    }

    .footer-fix-content{
        max-width: 200px;
    }
    .footer-bottom-content{
        margin-top:50px;
        margin-bottom:50px;
        border-top:1px solid lighten($thirdColor,15%);
        padding-top:15px;
        text-align: center;
        font-size:12px;
        color:#fff;
    
    }
    .footer-logo{
        max-width: 200px;
        display: block;
        margin-bottom:60px;
        img{
            width:100%;
        }
    }

    .footer-logo2{
        max-width: 200px;
        display: block;
        margin-bottom:20px;
        img{
            width:100%;
        }
    }

    .address{
        font:14px/1.4 $nunitor;
        color:#fff;
        margin-bottom: 25px;

        + .address{
            padding-top:25px;
            border-top:1px solid lighten($thirdColor,15%);
        }
    }
    .description{
        font:14px/1.4 $nunitor;
        color:#fff;
        margin-bottom: 25px;

        + .address{
            padding-top:25px;
            border-top:1px solid lighten($thirdColor,15%);
        }
    }


    ul{
        padding-left:0;
        margin-bottom: 45px;
        li{
            margin-bottom: 15px;
            a{
                color:#fff;
            }
        }
    }

    .linkUnder{
        text-decoration: underline;
    }

    form{
        text-align: right;
        label{
            display: block;
            margin-bottom:25px;
        }
        input{
            max-width:325px;
            width:100%;
            height:45px;
            border-radius: 5px;
            border:none;
            padding-right:15px;
            text-align:right;
            margin-bottom:25px;
        }
    }

    .social{
        text-align: right;
        margin-top:20px;
        a{
            display: inline-block;
            border-radius:5px;
            width:35px;
            height:35px;
            line-height: 32px;
            margin-left:5px;
            padding-left:2px;
            text-align: center;
            transition:all 0.4s;
            &:hover{
                background-color:$secondaryColor;
            }
        }
    }

    @include custom_max(991px){
        form{
            margin-top: 50px;
            text-align: center;

            input{
                border-radius: 50px;
                text-align: left;
                margin-right:15px;
                padding-left:15px;
            }
        }
        .social{
            text-align: center;
        }
    }

    @include custom_max(575px){
        text-align: center;
        .footer-fix-content{
            max-width: 100%;
        }
        .footer-logo{
            margin-left:auto;
            margin-right:auto;
            margin-bottom:30px;
        }

        form{
            input{
                margin-right:0;
                max-width: 100%;
            }
        }
    }
}