.navbar{
    position: absolute;
    left:0;
    right:0;
    top:0;
    z-index: 99;
    &-light{
        background-color:transparent;
    }

    &-brand{
        display: block;
        max-width:200px;
    }

   
    &-nav{
        .nav{
            &-item{
                display: flex;
                align-items: center;
                text-align:center;
            }
            &-link{
                font:14px $nunitosb;
                color:#fff;
                padding:0 20px;
                display: block;
                &.button{
                    padding:10px 30px;
                    background-color:$baseColor;
                    border-radius:100px;
                    margin-left: 15px;
                }
            }
        }
    }

    @include custom_max(991px){
        &-toggler{
   
            position: relative;
            z-index:1;
            &:focus{
                outline: none;
            }
            &-icon{
                width:30px;
                height:2px;
                display: block;
                padding:0;
                background: #fff;
                border-radius:5px;
                position: relative;
                &::before{
                    content:"";
                    display: block;
                    position: absolute;
                    top:-8px;
                    left:10px;
                    right:0;
                    height:2px;
                    background-color:#fff;
                }
                &::after{
                    content:"";
                    display: block;
                    position: absolute;
                    bottom:-8px;
                    left:5px;
                    right:0;
                    height:2px;
                    background-color:#fff;
                }
            }
        }
        &-nav{
            background-color:#fff;
            padding-bottom: 15px;
            padding-top:15px;
            border-radius: 10px;
            .nav{
                &-item{
                    text-align: center;
                }
                &-link{
                    color:#000;
                    padding:10px 15px;
                    display: block;
                    width:100%;
                    &.button{
                        max-width: 300px;
                        color:#fff;
                        margin:0 auto;
                        &:hover{}
                    }
                    &:hover{
                        color:$baseColor;
                    }
                    
                }
            }
        }
    }
}