.section-products{
    padding-top:110px;
    position: relative;
    overflow: hidden;
    .row{
        margin:0;
    }
    .section-right{
        text-align: left;
   
        .section-header{
            text-align: left;
        }
    }

    .product-wrapper{
        margin-left:-20%;
        padding-right:50px;
        .product-item{
            padding:80px 10px;
            position: relative;
            &::before{
                content:'';
            }
            .image{
                border-radius: 15px;
                // max-width:500px;
                overflow: hidden;
                box-shadow: 0 8px 11px -2px rgba(1, 37, 43, 0.3);
            }
        }
    }
    .triangle{
        &-6{
            position:absolute;
            right: -4%;
            top:50%;
            transform: translateY(-50%);
            max-width: 160px;
            z-index: -1;

        }
    }

    @include custom_max(991px){
        .product-wrapper{
            margin-left:0;
            padding-right:0;
            max-width: 700px;
            margin: 0 auto;

        }

        .section-right{
            padding:0 !important;
            text-align:center;
            max-width: 700px;
            margin:0 auto;
            .section-header{
                text-align: center;
            }
        }
    }

    @include custom_max(767px){
        padding-top:80px;
        padding-bottom:10px;
        .row{
            flex-direction: column-reverse;
        }
        .product-wrapper{
            .product-item{
                .image{
                    border-radius: 5px;
                }
            }
        }
    }
}