.section-getting-started{
    padding-top: 150px;
    padding-bottom:150px;
    position: relative;
    .image{
        max-width:665px;
        margin:0 auto;
    }
    .description{
        color:$text;
        max-width: 700px;
        text-align: center;
        margin:0 auto 50px;
    }
    .triangle{
        &-6,&-9,&-10,&-8{
            position:absolute;
            z-index:-1;
        }
        &-6{
            
            left: -2%;
            top:50%;
            transform: translateY(-50%);
            max-width: 180px;

        }

        &-10{
            bottom:10%;
            right:12%;
        }
        &-9{
            bottom:9%;
            right:9%;
            max-width: 170px;
        }   
    }
    @include custom_max(991px){
        padding-bottom: 80px;
        padding-top: 80px;

    
    }
}